@media screen and (max-width: 550px) {
    body{
        display: flex;
        align-items: center;
        align-self: center;
        min-width: 800px;
        min-height: 600px;
        grid-template-columns: repeat(6,3fr);
        grid-template-rows: repeat(6,3fr);
     
    }
    .GameContainer{
        display: flex-box;
        justify-content: center;
        align-self: center;
        text-align: center;
        margin-right: 2%;
        margin: 5% 1% auto;
        max-width: 450px;
        max-height: 500px;
        /* background-image: ; */
        
    }

    .BlackJack{
        display: flex;
        background-color: rgba(85, 85, 85, 0.993);
        background-image:image('/src/assets/bjheader.jpg');
        justify-content: center;
        text-align: center;
        margin-right: 10%;
        margin: auto;
        min-width: 400px;
        min-height: 600px;
        font-size: 150%;
        font-weight: Bold;
        grid-template-columns: repeat(9, 3fr);
        grid-template-rows: repeat(9, 3fr);
        padding: 2%;
        
    }
    .title{
        text-align: center;
        justify-content: center;
        align-content: inherit;
        font-size: 200%;
        grid-column: 5;
        grid-row: 3;
    }
    .headerIMG{
        display: flex;
        grid-row: 1 span;
        /* opacity: 0; */
    }
    .start{
        background-color: rgb(59, 160, 126);
        color: oldlace;
        /* font-weigh; */
        grid-column: 4/span 3;
        grid-row: 6 /span 1;
        font-size: 100%;
    }

    .Dealer{
        display: flex;
        font-size: 150%;
    }
    .DealerButton{
        background-color: red;
    }
    li{
        display: inline-flexbox;
        align-content: space-around;
        flex-direction: row;
    }
    .PlayerValues{
        display: flexbox;
        justify-content:center;
        align-items: inherit;
        text-align: center;
        margin-right: 10%;
        margin: auto;
        min-width: 400px;
        min-height: 200px;
        font-size: 150%;
        font-weight: Bold;
        grid-row: 1;
    }
    .PlayerValues h3{
        text-align: center;
        justify-content: center;
        font-size: 150%;
        grid-row: 1;
        grid-column: 2;
        
    }
    .PlayerCards{
        display: inline-flex;
        justify-content:center;
        top: 0;
        flex-direction: row;
        min-width: 5px;
        min-height: 5px
    }
    .DealerCards{
        display: inline-flex;
        flex-direction: row;
        min-width: 5px;
        min-height: 5px
    }
    #CardImagePlayer, #CardImageDealer{
        justify-items:center;
        justify-content: space-evenly;
        align-items: inherit;
        align-self: flex-start;
        align-content: space-around;
        flex-direction: row;
        max-width: 100px;
        max-height: 200px;
    
    }
}

